@font-face {
    font-family: "Open Sans";
    src: local("Open Sans Light"),
      local("Open-Light"),
      url("../fonts/open-sans-300.woff2") format("woff2"),
      url("../fonts/open-sans-300.woff") format("woff");
    font-style: normal;
    font-weight: 300;
    font-display: swap;
  }
  

